import {
	mapState,
	mapActions,
	mapGetters,
	mapMutations
} from 'vuex'
import {
	uniPopup
} from "@dcloudio/uni-ui"

export default {

	onLoad() {
		if (this.isLogin) {
			this.getMessageListAction('?offset=0&limit=10&category=socialwork')
		}
	},

	computed: {
		...mapState('authed', [
			'isLogin',
		]),
		...mapState('message', [
			'messageList',
		]),

	},

	components: {
		uniPopup,
	},

	methods: {
		...mapActions('message', [
			'getMessageListAction',
		]),

	}
}
